import { sanityClient } from '@/lib/sanity';
import { notFound } from 'next/navigation';

async function getNewsItem(slug) {
  return await sanityClient.fetch(
    `*[_type == "news" && slug.current == $slug][0] {
      title,
      excerpt,
      "slug": slug.current,
      "image": image.asset->url,
      body,
      category,
      author,
      publishedAt
    }`,
    { slug }
  );
}

// ðŸŽ¯ Meta tags dinÃ¢micas (OG tags)
export async function generateMetadata({ params }) {
  const news = await getNewsItem(params.slug);
  
  if (!news) return {};

  return {
    title: news.title,
    description: news.excerpt,
    openGraph: {
      title: news.title,
      description: news.excerpt,
      images: [
        {
          url: news.image || '/og-default.jpg',
          width: 1200,
          height: 630,
          alt: news.title,
        }
      ],
      type: 'article',
    },
    twitter: {
      card: 'summary_large_image',
      title: news.title,
      description: news.excerpt,
      images: [news.image || '/og-default.jpg'],
    },
  };
}

export default async function NewsItemPage({ params }) {
  const news = await getNewsItem(params.slug);

  if (!news) {
    notFound();
  }

  return (
    <article className="container mx-auto px-4 py-8 max-w-4xl">
      {news.image && (
        <img 
          src={news.image} 
          alt={news.title} 
          className="w-full h-96 object-cover rounded-lg mb-8"
        />
      )}
      <h1 className="text-4xl font-bold mb-4">{news.title}</h1>
      <p className="text-gray-600 mb-8">{news.excerpt}</p>
      
      <div className="flex gap-4 text-sm text-gray-500 mb-8">
        {news.author && <span>Por {news.author}</span>}
        {news.publishedAt && (
          <span>{new Date(news.publishedAt).toLocaleDateString('pt-BR')}</span>
        )}
        {news.category && <span className="bg-blue-100 px-2 py-1 rounded">{news.category}</span>}
      </div>

      <div className="prose max-w-none">
        {/* Aqui vocÃª renderiza o body - se for Portable Text precisa de um componente especÃ­fico */}
        {news.body}
      </div>
    </article>
  );
}